.Player {
    z-index: 999999999999;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px;
    cursor: pointer;
    -webkit-transition: all .1s linear;
    transition: all .1s linear;
}

.Player:hover {
    -webkit-transform: translate(0px, -20px);
            transform: translate(0px, -20px);
}

.Player p {
    font-size: 16px;
    font-family: 'Press Start 2P';
}

.Menu-choose {
    z-index: 9999999999;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-animation: bounceInDown 1s ease;
            animation: bounceInDown 1s ease;
}

.Menu-title {
    font-size: 30px;
    font-family: 'Press Start 2P';
    text-align: center;
    padding: 20px 0px;
}

.Menu-body {
    width: 55%;
    height: 70%;
    background: rgba(255, 255, 255, .9);
    padding: 25px;
    border-radius: 20px;
    border: 2px solid;
    overflow: auto;
    position: relative;
}

.Menu-list {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}

.Menu-close-btn {
  height: 55px;
  width: 55px;
  background: transparent;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.Menu-close-btn:hover {
  background: #dcdcdc;
}

.Menu-close-btn img {
  width: 45px;
}

@-webkit-keyframes bounceInDown {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
  
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }
  
    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }
  
    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }
  
    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}

@keyframes bounceInDown {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
  
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }
  
    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }
  
    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }
  
    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
}


.Menu {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    overflow: auto;
    overflow-x: hidden;
}

.Logo, .Menu-nav {
    -webkit-animation: fadeIn 1s ease;
            animation: fadeIn 1s ease;
}

.Logo {
    margin: 80px auto 120px;
    text-align: center;
    z-index: 9999;
}

.Logo h1 {
    font-size: 80px;
    background: url('https://ak7.picdn.net/shutterstock/videos/1023423877/thumb/1.jpg');
    background-size: cover;
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Press Start 2P';
}

.Logo h2 {
    font-size: 40px;
    color: #fff;
    font-family: 'Press Start 2P';
}

.Menu-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Menu-nav button {
    margin: 0px 0px 30px;
    width: 200px;
    height: 50px;
    background: transparent;
    border: 2px solid #fff;
    border-radius: 12px;
    font-size: 18px;
    font-family: 'Press Start 2P';
    cursor: pointer;
    position: relative;
    z-index: 9999;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
}

.Menu-nav button::after {
    content: '';
    display: none;
    background: dodgerblue;
    height: 50px;
    width: 200px;
    position: absolute;
    -webkit-transform: translate3d(100%, 100%);
            transform: translate3d(100%, 100%);
}

.Menu-nav button:hover {
    border: 3px solid #fff;
}

.Menu-git {
    margin: 250px 0px 0px;
}

.Menu-git a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    padding: 10px;
    border-radius: 5px;
    z-index: 9999;
    font-weight: 600;
}

.Menu-git a:hover {
    border-bottom: 1px solid #fff;
}

