button {
  outline: none;
}

.App {
  background: -webkit-gradient(linear, left bottom, left top, color-stop(5%, #00FFFF), color-stop(70%, #1E90FF), to(#0f77f5));
  background: linear-gradient(to top, #00FFFF 5%, #1E90FF 70%, #0f77f5);
  display: block;
  margin: 5% auto;
  position: relative;
  overflow-x: hidden;
  -webkit-animation: fadeIn 1s ease;
          animation: fadeIn 1s ease;
}

.App-big-title {
  width: 90%;
  position: absolute;
  text-align: center;
  top: 5%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  /* animation: name duration timing-function delay iteration-count direction fill-mode; */
  z-index: 99999;
}

.App-big-title p {
  opacity: 0;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  font-family: 'Press Start 2P', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  -webkit-animation: fadeTitle infinite .7s linear 1s;
          animation: fadeTitle infinite .7s linear 1s;
  font-size: 70px;
}

.App-replay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.App-replay button {
  margin: 0px 0px 30px;
  width: 200px;
  height: 50px;
  background: rgba(255, 255, 255, 0.7);
  border: 2px solid #dcdcdc;
  border-radius: 12px;
  font-size: 18px;
  font-family: 'Press Start 2P';
  cursor: pointer;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  -webkit-animation: fadeIn 1s ease;
          animation: fadeIn 1s ease;
}

.App-replay button:hover {
  border: 3px solid #dcdcdc;
}

.player-score {
  background: #fff;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 200px;
  width: 200px;
  font-family: 'Press Start 2P', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.player-score.right {
  border-radius: 10px 0px 0px 10px;
  top: 200px;
  right: 0;
  -webkit-animation: fadeInRight .4s ease;
          animation: fadeInRight .4s ease;
}

.player-score.left {
  border-radius: 0px 10px 10px 0px;
  top: 200px;
  left: 0;
  -webkit-animation: fadeInLeft .4s ease;
          animation: fadeInLeft .4s ease;
}

@-webkit-keyframes fadeTitle {
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes fadeTitle {
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.5;
  }
}

@-webkit-keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


